import React, { useEffect } from 'react';
import { useParams } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import { useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom";

import KajiLibraryApi from '../../utils/KajiLibraryApi';
import BookCard from '../../components/BookCard';
import SearchForm from '../../components/SearchForm';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        flexWrap: 'wrap',
    },
    search: {
        margin: '0 auto',
    },
    cards: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        flexWrap: 'wrap',
        width: '90vw',
    },
    card: {
        margin: theme.spacing(2),
    },
    page: {
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
        width: '90vw',
    },
}));

export default function BookListPage() {
    const classes = useStyles();
    // const params = Number(useParams().page);

    // const offset = params * 10 - 10;
    const limit = 10;
    const history = useHistory();
    const [books, setBooks] = React.useState();
    const [value, setValue] = React.useState('');
    const [params, setParams] = React.useState(Number(useParams().page));
    const init = React.useCallback(async () => {
        const limitBooks = await KajiLibraryApi().books.getBooksLimitList(params*10-10,limit);
        console.log(limitBooks)
        setBooks(limitBooks)
    }, []);
    const location = useLocation();
    useEffect(() => {
        console.log(location.pathname);
        init();
      }, [init]);
    const handleChange = async (event) => {
        setValue(event.target.value);
        const data = await KajiLibraryApi().books.findByFilter({
            where: {
                "title": {
                    "regexp": event.target.value ? `/${event.target.value}/` : ".*"
            }}
        })
        setBooks(data);
        console.log(data);
    }

    const bookCards = books ? books.map((book) => {
        return <BookCard key={book.id} id={book.id}  className={classes.card} /> 
    }): "";
    
    return (
        <div className={classes.root}>
            <SearchForm 
                value={value} 
                onChange={handleChange} 
                className={classes.search}
            />
            <div className={classes.cards}>
                {bookCards}
            </div>
            <div className={classes.page}>
                <Button
                    onClick = { () => {
                        console.log("end")
                        history.push(`/bookList/${params + 1}`);
                    } }
                >
                    次へ
                </Button>
            </div>
        </div>
    );
}